.option {
  display: flex;
  justify-content: space-between;
  padding: $l-size $m-size;
  border-bottom: 0.1rem solid lighten($light-blue, 10%);
}

.option--text {
  color: #fff;
  font-weight: 500;
  font-size: 2rem;
  margin: 0;
  word-break: break-all;
}


